.footer_container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  height: 62vh;
  border-bottom: 1px solid hwb(0 27% 73%);
  font-family: "Amazon Ember", Arial, sans-serif;
}

.outer_wrapper {
  background-color: #232f3e;
}

.footer_wrapper {
  width: 950px;
  display: flex;
  justify-content: space-between;
  margin-top: -40px;
}

.footer_wrapper ul {
  list-style: none;
}

.footer_wrapper ul li a {
  color: rgb(236, 229, 229);
  text-decoration: none;
  font-size: 13px;
  padding: 5px;
}

.footer_wrapper ul li a:hover {
  text-decoration: underline;
}

.footer_wrapper ul li {
  margin: 10px 0;
}

.btn_button {
  background-color: #37475a;
  padding: 16px;
  cursor: pointer;
  text-align: center;
}

.btn_button p {
  background-color: #37475a;
  color: white;
  font-size: 14px;
  font-weight: bolder;
}

.ft_footer {
  width: 50%;
  background-color: #232f3e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 44px;
}

.ft_footer a {
  font-family: "Amazon Ember", Arial, sans-serif;
  margin-top: 30px;
  width: 15%;
  color: rgb(211, 207, 207);
  text-decoration: none;
  height: 10px;
  font-size: 12px;
}

.ft_footer a h5 {
  font-size: 11px;
}
.footer_logo img {
  width: 100%;
  object-fit: contain;
  height: 30px;
}

.ft_footer a:nth-child(2) .ft_footer a:nth-child(4) {
  display: flex;
  justify-content: center;
  gap: 10px;
  align-items: center;
}

.footer_language img {
  width: 15%;
}

.ft_footer a:nth-child(2),
.ft_footer a:nth-child(3),
.ft_footer a:nth-child(4) {
  border: 1px solid rgb(142, 136, 136);
  padding: 10px 20px;
  border-radius: 2px;
}
